import { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

// Import pages
import Navigation from './components/Navigation.tsx';
import Home from './pages/Home.tsx';
import Configure from './pages/Configure.tsx';
import Manage from './pages/Manage.tsx';

// Import types
import type { MongoDBCredentials } from './types';

function App() {
  const [MongoDBCred, setMongoDBCred] = useState<MongoDBCredentials>({
    user: null,
    pass: null,
    cluster: null,
  });
  const [hasCredentials, setHasCredentials] = useState<boolean>(false);

  // Load MongoDB Credentials from environment variables
  useEffect(() => {
    const envUser = import.meta.env.VITE_MONGO_USERNAME;
    const envPass = import.meta.env.VITE_MONGO_PASSWORD;
    const envCluster = import.meta.env.VITE_MONGO_CLUSTER_URL;

    console.log('Environment variables loaded:', {
      envUser: envUser ? 'SET' : 'NOT SET',
      envPass: envPass ? 'SET' : 'NOT SET',
      envCluster: envCluster ? 'SET' : 'NOT SET',
      actualValues: {
        user: envUser,
        pass: envPass ? '***' : null,
        cluster: envCluster
      }
    });

    if (envUser && envPass && envCluster) {
      console.log('Setting credentials from environment variables');
      setMongoDBCred({
        user: envUser,
        pass: envPass,
        cluster: envCluster,
      });
      setHasCredentials(true);
    } else {
      console.log('Environment variables not complete, setting hasCredentials to false');
      setHasCredentials(false);
    }
  }, []);

  // Check if we have valid credentials
  const hasValidCredentials = hasCredentials && MongoDBCred.user && MongoDBCred.pass && MongoDBCred.cluster;

  return (
    <BrowserRouter>
      <div style={{ minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
        <Navigation />
        <div style={{ flexGrow: 1, padding: '20px' }}>
          {/* Debug Info - Remove this later */}
          <div style={{ background: '#f0f0f0', padding: '10px', margin: '10px 0', borderRadius: '4px', fontSize: '12px' }}>
            <strong>Debug Info:</strong><br />
            envUser: {import.meta.env.VITE_MONGO_USERNAME ? 'SET' : 'NOT SET'}<br />
            envPass: {import.meta.env.VITE_MONGO_PASSWORD ? 'SET' : 'NOT SET'}<br />
            envCluster: {import.meta.env.VITE_MONGO_CLUSTER_URL ? 'SET' : 'NOT SET'}<br />
            hasCredentials: {hasCredentials.toString()}<br />
            hasValidCredentials: {hasValidCredentials ? 'true' : 'false'}
          </div>

          <Routes>
            <Route path="/configure" element={
              <Configure mongoDBCred={MongoDBCred} setMongoDBCred={setMongoDBCred} />
            } />
            <Route path="/manage" element={
              hasValidCredentials ? (
                <Manage mongoDBCred={MongoDBCred} setMongoDBCred={setMongoDBCred} />
              ) : (
                <Navigate to="/configure" replace />
              )
            } />
            <Route path="/" element={
              hasValidCredentials ? (
                <Home />
              ) : (
                <Navigate to="/configure" replace />
              )
            } />
            <Route path="*" element={
              hasValidCredentials ? (
                <Home />
              ) : (
                <Navigate to="/configure" replace />
              )
            } />
          </Routes>
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
